/* ===== SKILLS SECTION - FRESH REDESIGN ===== */

.skills-section {
  position: relative;
  background: linear-gradient(135deg, #fafafa 0%, #ffffff 50%, #f8f9fa 100%);
  overflow: hidden !important;
}

/* Background Shapes */
.skills-bg-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 0;
  pointer-events: none;
}

.bg-shape {
  position: absolute;
  opacity: 0.06;
  animation: floatShape 10s ease-in-out infinite;
}

.bg-shape-1 {
  width: 500px;
  height: 500px;
  background: radial-gradient(circle, #7a38c8, transparent);
  border-radius: 50%;
  top: -10%;
  right: 5%;
  filter: blur(100px);
}

.bg-shape-2 {
  width: 400px;
  height: 400px;
  background: radial-gradient(circle, #00bcd4, transparent);
  border-radius: 50%;
  bottom: -10%;
  left: 10%;
  filter: blur(90px);
  animation-delay: 3s;
}

.bg-shape-3 {
  width: 350px;
  height: 350px;
  background: radial-gradient(circle, #facc15, transparent);
  border-radius: 50%;
  top: 40%;
  left: 40%;
  filter: blur(80px);
  animation-delay: 6s;
}

@keyframes floatShape {
  0%, 100% {
    transform: translate(0, 0) scale(1);
  }
  33% {
    transform: translate(30px, -30px) scale(1.1);
  }
  66% {
    transform: translate(-20px, 20px) scale(0.9);
  }
}

/* Main Wrapper */
.skills-wrapper {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  min-width: 100vw;
  height: 100vh;
  padding: 0 80px;
  gap: 80px;
  box-sizing: border-box;
}

/* ===== LEFT SECTION ===== */
.skills-left {
  flex: 0 0 450px;
  display: flex;
  flex-direction: column;
}

.skills-content-left {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

/* Badge */
.skill-badge {
  display: inline-flex;
  align-items: center;
  gap: 12px;
  padding: 12px 24px;
  background: rgba(122, 56, 200, 0.08);
  border: 2px solid rgba(122, 56, 200, 0.15);
  border-radius: 50px;
  width: fit-content;
  animation: badgeBounce 3s ease-in-out infinite;
}

@keyframes badgeBounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
}

.badge-icon {
  font-size: 20px;
  animation: rotate 4s linear infinite;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.badge-label {
  font-size: 13px;
  font-weight: 700;
  color: #7a38c8;
  text-transform: uppercase;
  letter-spacing: 1.5px;
}

/* Title */
.skills-main-title {
  font-size: 4rem;
  font-weight: 800;
  line-height: 1.1;
  color: #1a1a1a;
  margin: 0;
  letter-spacing: -1px;
}

.title-gradient {
  background: linear-gradient(135deg, #7a38c8 0%, #00bcd4 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  display: inline-block;
  animation: gradientSlide 3s ease infinite;
  background-size: 200% 200%;
}

@keyframes gradientSlide {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

/* Description */
.skills-description {
  margin: 0;
}

.skills-description p {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.8;
  color: #5a5a5a;
  margin: 0;
  padding-left: 25px;
  border-left: 4px solid transparent;
  border-image: linear-gradient(to bottom, #7a38c8, #00bcd4);
  border-image-slice: 1;
}

/* Stats */
.skills-stats {
  display: flex;
  align-items: center;
  gap: 30px;
  margin-top: 10px;
}

.stat-item {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.stat-number {
  font-size: 36px;
  font-weight: 800;
  background: linear-gradient(135deg, #7a38c8 0%, #00bcd4 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1;
}

.stat-label {
  font-size: 12px;
  font-weight: 600;
  color: #6a6a6a;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: black;
}

.stat-divider {
  width: 2px;
  height: 40px;
  background: linear-gradient(to bottom, transparent, #7a38c8, transparent);
}

/* ===== RIGHT SECTION ===== */
.skills-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.tech-grid-container {
  width: 100%;
  max-width: 700px;
}

.tech-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
}

/* Tech Cards */
.tech-card {
  position: relative;
  aspect-ratio: 1;
  background: white;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  border: 2px solid rgba(122, 56, 200, 0.08);
  overflow: hidden;
}

.tech-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(122, 56, 200, 0.03), rgba(0, 188, 212, 0.03));
  opacity: 0;
  transition: opacity 0.3s ease;
}

.tech-card:hover::before {
  opacity: 1;
}

.tech-card:hover {
  transform: scale(1.05);
  box-shadow: 0 15px 40px rgba(122, 56, 200, 0.15);
  border-color: rgba(122, 56, 200, 0.2);
}

.tech-card-inner {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 15px;
  padding: 25px;
  width: 100%;
  height: 100%;
}

.tech-logo {
  font-size: 48px;
  transition: all 0.4s ease;
}

.tech-card:hover .tech-logo {
  transform: scale(1.1);
}

.tech-label {
  font-size: 13px;
  font-weight: 600;
  color: #4a4a4a;
  text-align: center;
  opacity: 1;
  transform: translateY(0);
  transition: all 0.3s ease;
}

.tech-card:hover .tech-label {
  opacity: 1;
  transform: translateY(0);
  color: #7a38c8;
}

/* Glow Effect */
.tech-glow {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 3px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.tech-card:hover .tech-glow {
  opacity: 0.6;
  box-shadow: 0 0 20px currentColor;
}

/* ===== RESPONSIVE DESIGN ===== */

@media (max-width: 1400px) {
  .skills-wrapper {
    gap: 60px;
    padding: 0 6%;
  }

  .skills-main-title {
    font-size: 3.5rem;
  }

  .tech-grid {
    grid-template-columns: repeat(4, 1fr);
    gap: 18px;
  }
}

@media (max-width: 1200px) {
  .skills-wrapper {
    gap: 50px;
    padding: 0 5%;
  }

  .skills-left {
    flex: 0 0 400px;
  }

  .skills-main-title {
    font-size: 3rem;
  }

  .tech-logo {
    font-size: 36px;
  }
}

@media (max-width: 1024px) {
  .skills-wrapper {
    flex-direction: column;
    justify-content: center;
    padding: 5% 5%;
    gap: 60px;
  }

  .skills-left {
    flex: none;
    width: 100%;
    max-width: 600px;
  }

  .skills-content-left {
    align-items: center;
    text-align: center;
  }

  .skills-description p {
    border-left: none;
    border-top: 4px solid transparent;
    border-image: linear-gradient(to right, #7a38c8, #00bcd4);
    border-image-slice: 1;
    padding-left: 0;
    padding-top: 20px;
  }

  .skills-right {
    width: 100%;
  }

  .tech-grid-container {
    max-width: 100%;
  }

  .tech-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (max-width: 768px) {
  .skills-main-title {
    font-size: 2.5rem;
  }

  .skills-description p {
    font-size: 16px;
  }

  .tech-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
  }

  .tech-logo {
    font-size: 32px;
  }

  .stat-number {
    font-size: 28px;
  }

  .skills-stats {
    gap: 20px;
  }
}

@media (max-width: 640px) {
  .skills-wrapper {
    padding: 8% 5%;
  }

  .skills-main-title {
    font-size: 2rem;
  }

  .tech-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 12px;
  }

  .tech-logo {
    font-size: 28px;
  }

  .tech-label {
    font-size: 10px;
  }

  .stat-number {
    font-size: 24px;
  }

  .badge-label {
    font-size: 11px;
  }
}

@media (max-width: 480px) {
  .skills-main-title {
    font-size: 1.8rem;
  }

  .tech-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .skills-stats {
    flex-wrap: wrap;
    justify-content: center;
  }
}
