body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@font-face {
  font-family: 'Gotham';
  src: url('../public/assets/fonts/GothamMedium.ttf') format('truetype'),
       url('../public/assets/fonts/GothamLight.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Gotham';
  src: url('../public/assets/fonts/GothamLight.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Gotham';
  src: url('../public/assets/fonts/GothamLight.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
}

/* Apply the font globally */
body {
  font-family: 'Gotham', sans-serif; /* Fallback to sans-serif if Gotham is not available */
}
