/* ===== HOME SECTION - ADVANCED STYLES ===== */

.home-section {
  position: relative;
  overflow: hidden !important;
  background: linear-gradient(135deg, #ffffff 0%, #f8f9fa 50%, #ffffff 100%);
}

/* Custom Cursor */
.custom-cursor {
  position: fixed;
  width: 20px;
  height: 20px;
  border: 2px solid #7a38c8;
  border-radius: 50%;
  pointer-events: none;
  z-index: 9999;
  transition: all 0.1s ease;
  transform: translate(-50%, -50%);
  mix-blend-mode: difference;
}

/* Animated Background Elements */
.background-elements {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 0;
  pointer-events: none;
}

.floating-shape {
  position: absolute;
  opacity: 0.15;
  animation: float 6s ease-in-out infinite;
}

.shape-1 {
  top: 10%;
  left: 10%;
}

.shape-2 {
  bottom: 15%;
  right: 15%;
  animation-delay: 2s;
}

.shape-3 {
  top: 50%;
  right: 20%;
  animation-delay: 4s;
}

.gradient-circle {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  background: linear-gradient(135deg, #7a38c8, #00bcd4);
  filter: blur(60px);
}

.gradient-square {
  width: 250px;
  height: 250px;
  border-radius: 30px;
  background: linear-gradient(135deg, #facc15, #7a38c8);
  filter: blur(50px);
  transform: rotate(45deg);
}

.gradient-triangle {
  width: 0;
  height: 0;
  border-left: 150px solid transparent;
  border-right: 150px solid transparent;
  border-bottom: 260px solid #00bcd4;
  filter: blur(40px);
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(-20px) rotate(5deg);
  }
}

/* Floating Tech Icons */
.floating-icons {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  pointer-events: none;
}

.tech-icon {
  position: absolute;
  animation: floatIcon 4s ease-in-out infinite;
}

.icon-1 {
  top: 20%;
  left: 15%;
}

.icon-2 {
  top: 60%;
  right: 25%;
  animation-delay: 1.5s;
}

.icon-3 {
  bottom: 20%;
  left: 20%;
  animation-delay: 3s;
}

@keyframes floatIcon {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-15px);
  }
}

/* Home Content Layout */
.home-content {
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 100vw;
  height: 100vh;
  padding: 0 5%;
}

/* Left Content Section */
.intro {
  flex: 1;
  max-width: 600px;
  padding-right: 50px;
  padding-left: 40px;
}

.intro-wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Greeting Badge */
.greeting-badge {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 12px 24px;
  background: rgba(122, 56, 200, 0.1);
  border: 2px solid rgba(122, 56, 200, 0.2);
  border-radius: 50px;
  width: fit-content;
  backdrop-filter: blur(10px);
  animation: pulse 2s ease-in-out infinite;
}

.wave-emoji {
  font-size: 24px;
  animation: wave 1s ease-in-out infinite;
}

@keyframes wave {
  0%, 100% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(20deg);
  }
  75% {
    transform: rotate(-20deg);
  }
}

.greeting-text {
  font-size: 14px;
  font-weight: 600;
  color: #7a38c8;
  letter-spacing: 0.5px;
}

@keyframes pulse {
  0%, 100% {
    box-shadow: 0 0 0 0 rgba(122, 56, 200, 0.4);
  }
  50% {
    box-shadow: 0 0 0 10px rgba(122, 56, 200, 0);
  }
}

/* Main Heading */
.main-heading {
  font-size: 3.5rem;
  line-height: 1.2;
  margin: 0;
  font-weight: 700;
  color: #1a1a1a;
}

.gradient-text {
  background: linear-gradient(135deg, #7a38c8 0%, #00bcd4 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: gradientShift 3s ease infinite;
  background-size: 200% 200%;
}

@keyframes gradientShift {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

/* Role Container */
.role-container {
  display: flex;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
}

.role-prefix {
  font-size: 1.8rem;
  font-weight: 600;
  color: #4a4a4a;
  margin: 0;
}

.role-typed {
  font-size: 1.8rem;
  font-weight: 600;
  color: #7a38c8;
  margin: 0;
  min-width: 200px;
}

/* Description Section - Clean Style */
.description-section {
  margin: 10px 0;
  position: relative;
  padding-left: 20px;
  border-left: 3px solid transparent;
  border-image: linear-gradient(180deg, #7a38c8 0%, #00bcd4 100%);
  border-image-slice: 1;
}

.description-text {
  font-size: 17px;
  font-weight: 400;
  line-height: 1.9;
  color: #5a5a5a;
  margin: 0;
  letter-spacing: 0.3px;
}

/* Tech Stack Section */
.tech-stack-section {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin: 5px 0;
}

.tech-badge {
  padding: 10px 20px;
  background: transparent;
  color: #7a38c8;
  border: 2px solid #7a38c8;
  border-radius: 25px;
  font-size: 13px;
  font-weight: 600;
  letter-spacing: 0.5px;
  transition: all 0.3s ease;
  cursor: default;
  position: relative;
  overflow: hidden;
}

.tech-badge::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #7a38c8 0%, #00bcd4 100%);
  transition: left 0.3s ease;
  z-index: -1;
}

.tech-badge:hover {
  color: white;
  border-color: #7a38c8;
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(122, 56, 200, 0.3);
}

.tech-badge:hover::before {
  left: 0;
}

/* CTA Buttons */
.cta-buttons {
  display: flex;
  gap: 20px;
  margin-top: 10px;
  flex-wrap: wrap;
}

.btn-link {
  text-decoration: none;
}

/* Right Content Section - Image Container */
.home-image-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.image-wrapper {
  position: relative;
  width: 100%;
  max-width: 600px;
  height: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Decorative Elements */
.image-decoration {
  position: absolute;
  border-radius: 30px;
  animation: rotate360 20s linear infinite;
}

.decoration-1 {
  width: 400px;
  height: 400px;
  border: 3px solid rgba(122, 56, 200, 0.3);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.decoration-2 {
  width: 450px;
  height: 450px;
  border: 3px solid rgba(0, 188, 212, 0.2);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  animation-duration: 25s;
  animation-direction: reverse;
}

@keyframes rotate360 {
  from {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

/* Main Image Container */
.main-image-container {
  position: relative;
  z-index: 2;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.developer-gif {
  width: 80%;
  max-width: 450px;
  height: auto;
  border-radius: 30px;
  filter: drop-shadow(0 20px 40px rgba(0, 0, 0, 0.2));
  transition: transform 0.3s ease;
  animation: floatGif 3s ease-in-out infinite;
}

.developer-gif:hover {
  transform: scale(1.05);
}

@keyframes floatGif {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-15px);
  }
}

/* Floating Stats Cards */
.stat-card {
  position: absolute;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 20px 25px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
  text-align: center;
  border: 2px solid rgba(122, 56, 200, 0.2);
  transition: transform 0.3s ease;
  animation: cardFloat 3s ease-in-out infinite;
}

.stat-card:hover {
  transform: translateY(-5px) scale(1.05);
}

.stat-card-1 {
  top: 10%;
  right: -10%;
  animation-delay: 0.5s;
}

.stat-card-2 {
  bottom: 15%;
  left: -10%;
  animation-delay: 1.5s;
}

@keyframes cardFloat {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}

.stat-number {
  font-size: 32px;
  font-weight: 700;
  background: linear-gradient(135deg, #7a38c8 0%, #00bcd4 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 5px;
}

.stat-label {
  font-size: 12px;
  font-weight: 600;
  color: #000000;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Social Media Icons Enhancement */
.social-media-icons {
  margin-top: 10px;
}

.social-media-icons .MuiIconButton-root {
  background: rgba(122, 56, 200, 0.1);
  margin-right: 10px;
  transition: all 0.3s ease;
}

.social-media-icons .MuiIconButton-root:hover {
  background: rgba(122, 56, 200, 0.2);
  transform: translateY(-3px);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .main-heading {
    font-size: 2.8rem;
  }
  
  .role-prefix,
  .role-typed {
    font-size: 1.5rem;
  }
  
  .image-wrapper {
    max-width: 450px;
    height: 450px;
  }
}

@media (max-width: 768px) {
  .home-content {
    flex-direction: column;
    justify-content: center;
    padding: 0 20px;
  }
  
  .intro {
    max-width: 100%;
    padding-right: 0;
    padding-left: 0;
    text-align: center;
    margin-bottom: 40px;
  }
  
  .greeting-badge {
    margin: 0 auto;
  }
  
  .main-heading {
    font-size: 2.2rem;
  }
  
  .role-container {
    justify-content: center;
  }
  
  .role-prefix,
  .role-typed {
    font-size: 1.3rem;
  }
  
  .cta-buttons {
    justify-content: center;
  }
  
  .image-wrapper {
    max-width: 350px;
    height: 350px;
  }
  
  .stat-card-1 {
    right: 5%;
  }
  
  .stat-card-2 {
    left: 5%;
  }
  
  .tech-stack-badges {
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .main-heading {
    font-size: 1.8rem;
  }
  
  .role-prefix,
  .role-typed {
    font-size: 1.1rem;
  }
  
  .description-text {
    font-size: 15px;
  }
  
  .description-section {
    padding-left: 15px;
  }
  
  .tech-stack-section {
    justify-content: center;
  }
  
  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .cta-buttons button {
    width: 100%;
    max-width: 250px;
  }
}
