/* Contact Section Wrapper */
.contact-section {
  background: linear-gradient(135deg, #0a0015 0%, #1a0933 100%);
}

/* Contact Container */
.contact-container {
  min-height: 100vh;
  color: white;
  position: relative;
  overflow: hidden;
  padding: 40px 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Background Elements */
.contact-bg-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  z-index: 0;
  pointer-events: none;
}

.bg-circle {
  position: absolute;
  border-radius: 50%;
  filter: blur(100px);
  opacity: 0.4;
}

.bg-circle-1 {
  width: 600px;
  height: 600px;
  background: radial-gradient(circle, rgba(122, 56, 200, 0.5) 0%, transparent 70%);
  top: -100px;
  right: -100px;
}

.bg-circle-2 {
  width: 500px;
  height: 500px;
  background: radial-gradient(circle, rgba(0, 188, 212, 0.4) 0%, transparent 70%);
  bottom: -100px;
  left: -100px;
}

.bg-circle-3 {
  width: 400px;
  height: 400px;
  background: radial-gradient(circle, rgba(250, 204, 21, 0.3) 0%, transparent 70%);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Floating Emojis */
.floating-emojis {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  pointer-events: none;
}

.emoji {
  position: absolute;
  font-size: 40px;
  opacity: 0.3;
  animation: float 6s ease-in-out infinite;
}

.emoji-1 {
  top: 10%;
  left: 10%;
  animation-delay: 0s;
}

.emoji-2 {
  top: 20%;
  right: 15%;
  animation-delay: 1s;
}

.emoji-3 {
  top: 60%;
  left: 8%;
  animation-delay: 2s;
}

.emoji-4 {
  bottom: 20%;
  right: 10%;
  animation-delay: 0.5s;
}

.emoji-5 {
  top: 40%;
  right: 5%;
  animation-delay: 1.5s;
}

.emoji-6 {
  bottom: 15%;
  left: 15%;
  animation-delay: 2.5s;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0) rotate(0deg);
  }
  25% {
    transform: translateY(-20px) rotate(5deg);
  }
  50% {
    transform: translateY(0) rotate(-5deg);
  }
  75% {
    transform: translateY(20px) rotate(5deg);
  }
}

/* Contact Wrapper */
.contact-wrapper {
  max-width: 1200px;
  width: 100%;
  display: grid;
  grid-template-columns: 38% 62%;
  gap: 40px;
  position: relative;
  z-index: 2;
}

/* Left Section */
.contact-left {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.contact-badge {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  background: linear-gradient(135deg, rgba(122, 56, 200, 0.2), rgba(0, 188, 212, 0.2));
  border: 1px solid rgba(122, 56, 200, 0.4);
  padding: 10px 24px;
  border-radius: 50px;
  font-size: 15px;
  font-weight: 600;
  letter-spacing: 1px;
  color: #00bcd4;
  width: fit-content;
  backdrop-filter: blur(10px);
}

.badge-emoji {
  font-size: 20px;
  animation: wave 2s ease-in-out infinite;
}

@keyframes wave {
  0%, 100% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(20deg);
  }
  75% {
    transform: rotate(-10deg);
  }
}

.contact-title {
  font-size: 48px;
  font-weight: 900;
  line-height: 1.1;
  margin: 15px 0;
}

.title-gradient {
  background: linear-gradient(135deg, #7a38c8 0%, #00bcd4 50%, #facc15 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  background-size: 200% 200%;
  animation: gradient-shift 4s ease infinite;
}

@keyframes gradient-shift {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

.contact-description {
  font-size: 16px;
  line-height: 1.6;
  color: rgba(255, 255, 255, 0.8);
  max-width: 100%;
}

/* Contact Info Cards */
.contact-info-cards {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-top: 15px;
}

.info-card {
  display: flex;
  align-items: center;
  gap: 15px;
  background: linear-gradient(135deg, rgba(122, 56, 200, 0.1) 0%, rgba(0, 188, 212, 0.05) 100%);
  border: 1px solid rgba(122, 56, 200, 0.3);
  border-radius: 14px;
  padding: 15px;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  cursor: pointer;
}

.info-card:hover {
  transform: translateX(10px);
  border-color: rgba(0, 188, 212, 0.6);
  box-shadow: 0 10px 40px rgba(122, 56, 200, 0.3);
}

.info-icon {
  width: 45px;
  height: 45px;
  border-radius: 12px;
  background: linear-gradient(135deg, #7a38c8, #00bcd4);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.info-icon svg {
  width: 22px;
  height: 22px;
  color: white;
}

.info-content h4 {
  font-size: 15px;
  font-weight: 700;
  color: white;
  margin: 0 0 4px 0;
}

.info-content p {
  font-size: 13px;
  color: rgba(255, 255, 255, 0.7);
  margin: 0;
}

/* Social Links */
.social-links {
  margin-top: 15px;
}

.social-links h3 {
  font-size: 16px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 15px;
}

.social-icons {
  display: flex;
  gap: 15px;
}

.social-icon {
  width: 50px;
  height: 50px;
  border-radius: 12px;
  background: linear-gradient(135deg, rgba(122, 56, 200, 0.2), rgba(0, 188, 212, 0.2));
  border: 1px solid rgba(122, 56, 200, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  cursor: pointer;
}

.social-icon:hover {
  transform: translateY(-5px) scale(1.1);
  background: linear-gradient(135deg, #7a38c8, #00bcd4);
  box-shadow: 0 10px 30px rgba(122, 56, 200, 0.5);
}

.social-icon svg {
  width: 24px;
  height: 24px;
  color: white;
}

/* Right Section - Form */
.contact-right {
  display: flex;
  align-items: center;
  justify-content: center;
}

.form-container {
  width: auto;
  /* max-width: 100%; */
  background: linear-gradient(135deg, rgba(122, 56, 200, 0.08) 0%, rgba(0, 188, 212, 0.05) 100%);
  border: 1px solid rgba(122, 56, 200, 0.3);
  border-radius: 24px;
  padding: 35px;
  backdrop-filter: blur(20px);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
  margin-left: 40px;
  padding-right: 75px;
}

.form-container::before {
  content: '';
  position: absolute;
  top: -50%;
  right: -50%;
  width: 200px;
  height: 200px;
  background: radial-gradient(circle, rgba(122, 56, 200, 0.3) 0%, transparent 70%);
  border-radius: 50%;
  animation: pulse-glow 4s ease-in-out infinite;
}

@keyframes pulse-glow {
  0%, 100% {
    transform: scale(1);
    opacity: 0.5;
  }
  50% {
    transform: scale(1.2);
    opacity: 0.8;
  }
}

.form-header {
  margin-bottom: 30px;
}

.form-header h2 {
  font-size: 28px;
  font-weight: 800;
  margin-bottom: 8px;
  background: linear-gradient(135deg, #7a38c8, #00bcd4);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.form-header p {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.7);
}

/* Form Styles */
.contact-form {
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
  position: relative;
}

.form-group label {
  font-size: 13px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.9);
  letter-spacing: 0.5px;
  margin-left: 5px;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 14px 18px;
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid rgba(122, 56, 200, 0.3);
  border-radius: 12px;
  color: white;
  font-size: 15px;
  font-family: inherit;
  transition: all 0.3s ease;
  outline: none;
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.form-group input:focus,
.form-group textarea:focus,
.form-group input.focused,
.form-group textarea.focused {
  border-color: #00bcd4;
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 20px rgba(0, 188, 212, 0.3);
  transform: translateY(-2px);
}

.form-group textarea {
  resize: vertical;
  min-height: 100px;
}

/* Submit Button */
.submit-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  padding: 15px 35px;
  background: linear-gradient(135deg, #7a38c8, #00bcd4);
  border: none;
  border-radius: 12px;
  color: white;
  font-size: 15px;
  font-weight: 700;
  letter-spacing: 1px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  margin-top: 8px;
}

.submit-btn::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transform: translate(-50%, -50%);
  transition: width 0.5s, height 0.5s;
}

.submit-btn:hover::before {
  width: 300px;
  height: 300px;
}

.submit-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(122, 56, 200, 0.5);
}

.submit-btn:active {
  transform: translateY(-1px);
}

.submit-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.send-icon {
  width: 20px;
  height: 20px;
  transition: transform 0.3s ease;
  position: relative;
  z-index: 1;
}

.submit-btn:hover .send-icon {
  transform: translateX(5px);
}

/* Loading Spinner */
.spinner {
  width: 20px;
  height: 20px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-top-color: white;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Responsive Design */
@media (max-width: 1200px) {
  .contact-wrapper {
    grid-template-columns: 1fr;
    gap: 60px;
  }

  .contact-left {
    text-align: left;
  }

  .contact-badge {
    margin: 0;
  }

  .contact-title {
    font-size: 52px;
  }
}

@media (max-width: 768px) {
  .contact-container {
    padding: 60px 20px;
  }

  .contact-title {
    font-size: 40px;
  }

  .form-container {
    padding: 30px 20px;
      margin-left: 30px;
  }

  .form-header h2 {
    font-size: 26px;
  }

  .emoji {
    font-size: 30px;
  }

  .social-icons {
    justify-content: flex-start;
  }
}
