/* Music Section */
.music-section {
  background: linear-gradient(135deg, #0a0015 0%, #1a0933 100%);
}

/* Music Container */
.music-container {
  min-height: 100vh;
  color: white;
  position: relative;
  overflow: hidden;
  padding: 40px 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Background Elements */
.music-bg-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  z-index: 0;
  pointer-events: none;
}

.music-bg-circle {
  position: absolute;
  border-radius: 50%;
  filter: blur(120px);
  opacity: 0.4;
}

.music-bg-circle-1 {
  width: 550px;
  height: 550px;
  background: radial-gradient(circle, rgba(29, 185, 84, 0.5) 0%, transparent 70%);
  top: -100px;
  left: -100px;
}

.music-bg-circle-2 {
  width: 450px;
  height: 450px;
  background: radial-gradient(circle, rgba(122, 56, 200, 0.4) 0%, transparent 70%);
  bottom: -100px;
  right: -100px;
}

.music-bg-circle-3 {
  width: 400px;
  height: 400px;
  background: radial-gradient(circle, rgba(0, 188, 212, 0.3) 0%, transparent 70%);
  top: 50%;
  right: 20%;
}

/* Floating Music Notes */
.floating-notes {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  pointer-events: none;
}

.note {
  position: absolute;
  font-size: 35px;
  opacity: 0.25;
  animation: float-note 8s ease-in-out infinite;
}

.note-1 {
  top: 10%;
  left: 5%;
  animation-delay: 0s;
}

.note-2 {
  top: 25%;
  right: 8%;
  animation-delay: 1.5s;
}

.note-3 {
  top: 70%;
  left: 12%;
  animation-delay: 3s;
}

.note-4 {
  bottom: 15%;
  right: 15%;
  animation-delay: 0.8s;
}

.note-5 {
  top: 50%;
  right: 5%;
  animation-delay: 2s;
}

.note-6 {
  bottom: 25%;
  left: 8%;
  animation-delay: 2.8s;
}

.note-7 {
  top: 35%;
  left: 18%;
  animation-delay: 1.2s;
}

@keyframes float-note {
  0%, 100% {
    transform: translateY(0) rotate(0deg) scale(1);
  }
  25% {
    transform: translateY(-30px) rotate(10deg) scale(1.1);
  }
  50% {
    transform: translateY(0) rotate(-10deg) scale(0.9);
  }
  75% {
    transform: translateY(30px) rotate(10deg) scale(1.05);
  }
}

/* Equalizer Background */
.equalizer-bg {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: flex-end;
  gap: 8px;
  height: 150px;
  opacity: 0.15;
  z-index: 1;
}

.eq-bar {
  width: 6px;
  background: linear-gradient(180deg, #1db954, #7a38c8);
  border-radius: 3px;
  animation: equalizer 1.5s ease-in-out infinite;
}

@keyframes equalizer {
  0%, 100% {
    height: 20%;
  }
  50% {
    height: 100%;
  }
}

/* Music Wrapper */
.music-wrapper {
  max-width: 1200px;
  width: 100%;
  display: grid;
  grid-template-columns: 42% 58%;
  gap: 40px;
  position: relative;
  z-index: 2;
}

/* Left Section */
.music-left {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Music Badge */
.music-badge {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  background: linear-gradient(135deg, rgba(29, 185, 84, 0.2), rgba(122, 56, 200, 0.2));
  border: 1px solid rgba(29, 185, 84, 0.4);
  padding: 10px 24px;
  border-radius: 50px;
  font-size: 15px;
  font-weight: 600;
  letter-spacing: 1px;
  color: #1db954;
  width: fit-content;
  backdrop-filter: blur(10px);
}

.badge-note {
  font-size: 20px;
  animation: bounce-note 2s ease-in-out infinite;
}

@keyframes bounce-note {
  0%, 100% {
    transform: translateY(0) rotate(0deg);
  }
  50% {
    transform: translateY(-5px) rotate(10deg);
  }
}

/* Spotify Logo */
.spotify-logo-container {
  position: relative;
  width: fit-content;
  margin: 10px 0;
}

.spotify-logo {
  width: 140px;
  height: auto;
  position: relative;
  z-index: 2;
  filter: drop-shadow(0 0 20px rgba(29, 185, 84, 0.5));
  animation: pulse-logo 3s ease-in-out infinite;
}

@keyframes pulse-logo {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

.logo-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 180px;
  height: 180px;
  background: radial-gradient(circle, rgba(29, 185, 84, 0.3) 0%, transparent 70%);
  border-radius: 50%;
  animation: glow-pulse 3s ease-in-out infinite;
  z-index: 1;
}

@keyframes glow-pulse {
  0%, 100% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 0.5;
  }
  50% {
    transform: translate(-50%, -50%) scale(1.2);
    opacity: 0.8;
  }
}

/* Music Title */
.music-title {
  font-size: 48px;
  font-weight: 900;
  line-height: 1.1;
  margin: 10px 0;
}

.title-gradient {
  background: linear-gradient(135deg, #1db954 0%, #00bcd4 50%, #facc15 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  background-size: 200% 200%;
  animation: gradient-shift 4s ease infinite;
}

@keyframes gradient-shift {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

/* Music Description */
.music-description {
  font-size: 16px;
  line-height: 1.7;
  color: rgba(255, 255, 255, 0.8);
  margin: 10px 0;
}

/* Music Stats */
.music-stats {
  display: flex;
  flex-direction: row;
  gap: 12px;
  margin: 15px 0;
  flex-wrap: nowrap;
  align-items: center;
}

.stat-card {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  background: linear-gradient(135deg, rgba(29, 185, 84, 0.1) 0%, rgba(122, 56, 200, 0.05) 100%);
  border: 1px solid rgba(29, 185, 84, 0.3);
  border-radius: 12px;
  padding: 10px 14px;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  cursor: pointer;
  flex: 0 0 auto;
  width: auto;
}

.stat-card:hover {
  transform: translateY(-3px);
  border-color: #1db954;
  box-shadow: 0 10px 30px rgba(29, 185, 84, 0.3);
}

.stat-icon {
  width: 36px;
  height: 36px;
  border-radius: 10px;
  background: linear-gradient(135deg, #1db954, #00bcd4);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.stat-icon svg {
  width: 20px;
  height: 20px;
  color: white;
}

.stat-content {
  display: flex;
  flex-direction: column;
  gap: 2px;
  min-width: 0;
  flex-shrink: 0;
}

.stat-number {
  font-size: 20px;
  font-weight: 800;
  background: linear-gradient(135deg, #1db954, #00bcd4);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1;
  white-space: nowrap;
}

.stat-label {
  font-size: 11px;
  color: rgba(255, 255, 255, 0.7);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  white-space: nowrap;
}

/* Genre Tags */
.genre-tags {
  margin: 15px 0;
}

.genre-tags h4 {
  font-size: 16px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 12px;
}

.tags-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.genre-tag {
  padding: 8px 16px;
  background: rgba(29, 185, 84, 0.15);
  border: 1px solid rgba(29, 185, 84, 0.3);
  border-radius: 20px;
  font-size: 13px;
  font-weight: 600;
  color: #1db954;
  transition: all 0.3s ease;
  cursor: pointer;
  animation: fade-in-tag 0.5s ease forwards;
  opacity: 0;
}

@keyframes fade-in-tag {
  to {
    opacity: 1;
  }
}

.genre-tag:hover {
  background: rgba(29, 185, 84, 0.3);
  transform: scale(1.05);
  box-shadow: 0 5px 15px rgba(29, 185, 84, 0.3);
}

/* Play CTA Button */
.play-cta {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  padding: 16px 35px;
  background: linear-gradient(135deg, #1db954, #00bcd4);
  border: none;
  border-radius: 50px;
  color: white;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 1px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  margin-top: 15px;
  width: fit-content;
}

.play-cta::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transform: translate(-50%, -50%);
  transition: width 0.5s, height 0.5s;
}

.play-cta:hover::before {
  width: 300px;
  height: 300px;
}

.play-cta:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(29, 185, 84, 0.5);
}

.play-icon {
  width: 24px;
  height: 24px;
  position: relative;
  z-index: 1;
  transition: transform 0.3s ease;
}

.play-cta:hover .play-icon {
  transform: scale(1.2);
}

.play-cta span {
  position: relative;
  z-index: 1;
}

.pulse-ring {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  border: 3px solid rgba(29, 185, 84, 0.5);
  border-radius: 50px;
  animation: pulse-ring-animation 2s ease-out infinite;
}

@keyframes pulse-ring-animation {
  0% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -50%) scale(1.1);
    opacity: 0;
  }
}

.play-cta.playing {
  background: linear-gradient(135deg, #facc15, #1db954);
  animation: color-pulse 2s ease-in-out infinite;
}

@keyframes color-pulse {
  0%, 100% {
    box-shadow: 0 0 20px rgba(29, 185, 84, 0.5);
  }
  50% {
    box-shadow: 0 0 40px rgba(250, 204, 21, 0.8);
  }
}

/* Right Section - Player */
.music-right {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 100px;
}

.player-container {
  width: 100%;
  position: relative;
}

/* Player Decorations */
.player-decoration {
  position: absolute;
  width: auto;
  height: 100px;
  border-radius: 50%;
  filter: blur(40px);
  z-index: 0;
}

.player-decoration-top {
  background: radial-gradient(circle, rgba(29, 185, 84, 0.4) 0%, transparent 70%);
  top: -30px;
  right: -30px;
}

.player-decoration-bottom {
  background: radial-gradient(circle, rgba(122, 56, 200, 0.4) 0%, transparent 70%);
  bottom: -30px;
  left: -30px;
}

/* Player Frame */
.player-frame {
  position: relative;
  background: linear-gradient(135deg, rgba(29, 185, 84, 0.08) 0%, rgba(122, 56, 200, 0.05) 100%);
  border: 1px solid rgba(29, 185, 84, 0.3);
  border-radius: 24px;
  padding: 20px;
  backdrop-filter: blur(20px);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  z-index: 1;
}

/* Frame Header */
.frame-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid rgba(29, 185, 84, 0.2);
}

.frame-dots {
  display: flex;
  gap: 8px;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.dot-red {
  background: #ff5f56;
}

.dot-yellow {
  background: #ffbd2e;
}

.dot-green {
  background: #27c93f;
}

.frame-title {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #1db954;
  font-weight: 600;
  font-size: 14px;
}

.frame-icon {
  width: 18px;
  height: 18px;
}

/* Spotify Player */
.spotify-player {
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

/* Player Glow */
.player-glow {
  position: absolute;
  bottom: -50%;
  left: 50%;
  transform: translateX(-50%);
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, rgba(29, 185, 84, 0.3) 0%, transparent 70%);
  border-radius: 50%;
  animation: glow-pulse 4s ease-in-out infinite;
  z-index: 0;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .music-wrapper {
    grid-template-columns: 1fr;
    gap: 40px;
  }

  .music-title {
    font-size: 40px;
  }

  .music-stats {
    justify-content: flex-start;
    flex-wrap: wrap;
  }
}

@media (max-width: 768px) {
  .music-container {
    padding: 40px 20px;
  }

  .music-title {
    font-size: 32px;
  }

  .spotify-logo {
    width: 120px;
  }

  .note {
    font-size: 28px;
  }

  .music-stats {
    flex-wrap: wrap;
  }

  .stat-card {
    flex: 0 0 auto;
  }

  .play-cta {
    width: 100%;
  }

  .player-frame {
    padding: 15px;
  }
}
