/* Timeline Container */
.zigzag-timeline {
  min-height: 100vh;
  background: linear-gradient(135deg, #0a0015 0%, #1a0933 100%);
  display: flex;
  color: white;
  position: relative;
  overflow: hidden;
}

.timeline-wrapper {
  display: flex;
  width: 100%;
  min-height: 100vh;
}

/* Background Elements */
.timeline-bg-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  z-index: 0;
  pointer-events: none;
}

.bg-gradient {
  position: absolute;
  border-radius: 50%;
  filter: blur(80px);
  opacity: 0.3;
}

.bg-gradient-1 {
  width: 500px;
  height: 500px;
  background: radial-gradient(circle, rgba(122, 56, 200, 0.4) 0%, transparent 70%);
  top: 10%;
  left: 10%;
}

.bg-gradient-2 {
  width: 400px;
  height: 400px;
  background: radial-gradient(circle, rgba(0, 188, 212, 0.3) 0%, transparent 70%);
  top: 50%;
  right: 15%;
}

.bg-gradient-3 {
  width: 350px;
  height: 350px;
  background: radial-gradient(circle, rgba(250, 204, 21, 0.25) 0%, transparent 70%);
  bottom: 10%;
  left: 20%;
}

/* Left Section - Header */
.timeline-left {
  width: 35%;
  padding: 60px 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  position: sticky;
  top: 0;
  height: 100vh;
  background: linear-gradient(180deg, rgba(122, 56, 200, 0.05) 0%, rgba(0, 188, 212, 0.03) 100%);
  border-right: 1px solid rgba(122, 56, 200, 0.2);
  z-index: 2;
}

.timeline-content-left {
  width: 100%;
}

.timeline-badge {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  background: linear-gradient(135deg, rgba(122, 56, 200, 0.2), rgba(0, 188, 212, 0.2));
  border: 1px solid rgba(122, 56, 200, 0.4);
  padding: 8px 20px;
  border-radius: 50px;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 2px;
  text-transform: uppercase;
  margin-bottom: 30px;
  color: #00bcd4;
}

.badge-icon {
  font-size: 18px;
}

.badge-label {
  color: #00bcd4;
}

.timeline-title {
  font-size: 56px;
  font-weight: 800;
  line-height: 1.1;
  margin-bottom: 25px;
  background: linear-gradient(135deg, #7a38c8 0%, #00bcd4 50%, #facc15 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  background-size: 200% 200%;
  animation: gradient-shift 4s ease infinite;
}

.timeline-main-title {
  font-size: 56px;
  font-weight: 800;
  line-height: 1.1;
  margin-bottom: 25px;
  color: white;
}

.title-gradient {
  background: linear-gradient(135deg, #7a38c8 0%, #00bcd4 50%, #facc15 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  background-size: 200% 200%;
  animation: gradient-shift 4s ease infinite;
}

@keyframes gradient-shift {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

.timeline-description {
  font-size: 18px;
  line-height: 1.8;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 40px;
  max-width: 500px;
}

.timeline-description p {
  color: rgba(255, 255, 255, 0.8);
  margin: 0;
}

.timeline-stats {
  display: flex;
  align-items: center;
  gap: 30px;
  margin-top: 20px;
}

.stat-item {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.stat-number {
  font-size: 42px;
  font-weight: 800;
  background: linear-gradient(135deg, #7a38c8, #00bcd4);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.stat-value {
  font-size: 42px;
  font-weight: 800;
  background: linear-gradient(135deg, #7a38c8, #00bcd4);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.stat-label {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.7);
  text-transform: uppercase;
  letter-spacing: 1px;
}

.stat-divider {
  width: 2px;
  height: 50px;
  background: linear-gradient(180deg, rgba(122, 56, 200, 0.3), rgba(0, 188, 212, 0.3));
  border-radius: 2px;
}

/* Right Section - Timeline Content */
.timeline-right {
  width: 65%;
  padding: 80px 60px 80px 80px;
  position: relative;
  overflow-y: auto;
  max-height: 100vh;
}

/* Custom Scrollbar */
.timeline-right::-webkit-scrollbar {
  width: 8px;
}

.timeline-right::-webkit-scrollbar-track {
  background: rgba(122, 56, 200, 0.1);
  border-radius: 10px;
}

.timeline-right::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #7a38c8, #00bcd4);
  border-radius: 10px;
}

.timeline-right::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, #8b4ad4, #00d4f0);
}

/* Timeline Track - Vertical Line */
.timeline-track {
  position: absolute;
  left: 80px;
  top: 100px;
  bottom: 100px;
  width: 3px;
  background: linear-gradient(180deg, rgba(122, 56, 200, 0.3) 0%, rgba(0, 188, 212, 0.5) 50%, rgba(250, 204, 21, 0.3) 100%);
  border-radius: 2px;
  z-index: 1;
}

/* Timeline Items Container */
.timeline-items-container {
  position: relative;
  padding-left: 40px;
}

/* Individual Timeline Item */
.timeline-item {
  position: relative;
  margin-bottom: 60px;
  display: flex;
  align-items: flex-start;
  gap: 30px;
}

.timeline-item:last-child {
  margin-bottom: 0;
}

/* Timeline Node - Icon Circle */
.timeline-node {
  position: relative;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: linear-gradient(135deg, #7a38c8, #00bcd4);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  z-index: 2;
  box-shadow: 0 0 30px rgba(122, 56, 200, 0.6);
  transition: all 0.3s ease;
}

.timeline-node:hover {
  transform: scale(1.1);
  box-shadow: 0 0 50px rgba(122, 56, 200, 0.9);
}

.timeline-node svg {
  width: 28px;
  height: 28px;
  color: white;
}

/* Pulse Animation */
.timeline-node::before {
  content: '';
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  border-radius: 50%;
  border: 2px solid rgba(122, 56, 200, 0.4);
  animation: pulse-ring 2s ease-out infinite;
}

@keyframes pulse-ring {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    transform: scale(1.3);
    opacity: 0;
  }
}

/* Timeline Card */
.timeline-card {
  flex: 1;
  background: linear-gradient(135deg, rgba(122, 56, 200, 0.1) 0%, rgba(0, 188, 212, 0.05) 100%);
  border: 1px solid rgba(122, 56, 200, 0.3);
  border-radius: 20px;
  padding: 30px;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
  transition: all 0.4s ease;
  cursor: pointer;
  min-width: 140%;
}

.timeline-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(122, 56, 200, 0.15) 0%, rgba(0, 188, 212, 0.15) 100%);
  opacity: 0;
  transition: opacity 0.4s ease;
  z-index: 0;
}

.timeline-card:hover::before {
  opacity: 1;
}

.timeline-card:hover {
  transform: translateY(-5px);
  border-color: rgba(0, 188, 212, 0.6);
  box-shadow: 0 15px 50px rgba(122, 56, 200, 0.4);
}

/* Card Year Badge */
.card-year {
  display: inline-block;
  background: linear-gradient(135deg, #7a38c8, #00bcd4);
  padding: 6px 16px;
  border-radius: 20px;
  font-size: 13px;
  font-weight: 700;
  letter-spacing: 1px;
  margin-bottom: 15px;
  position: relative;
  z-index: 1;
}

/* Card Title */
.card-title {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 15px;
  color: white;
  position: relative;
  z-index: 1;
}

/* Card Description */
.card-description {
  font-size: 16px;
  line-height: 1.7;
  color: rgba(255, 255, 255, 0.75);
  position: relative;
  z-index: 1;
}

/* Glow Effect */
.timeline-card-glow {
  position: absolute;
  bottom: -100px;
  right: -100px;
  width: 200px;
  height: 200px;
  background: radial-gradient(circle, rgba(122, 56, 200, 0.3) 0%, transparent 70%);
  border-radius: 50%;
  pointer-events: none;
  z-index: 0;
  transition: all 0.4s ease;
}

.timeline-card:hover .timeline-card-glow {
  transform: translate(-20px, -20px);
  background: radial-gradient(circle, rgba(0, 188, 212, 0.4) 0%, transparent 70%);
}

/* Responsive Design */
@media (max-width: 1200px) {
  .timeline-left {
    width: 40%;
    padding: 40px 30px;
  }
  
  .timeline-right {
    width: 60%;
    padding: 60px 40px;
  }
  
  .timeline-title {
    font-size: 42px;
  }
  
  .stat-value {
    font-size: 36px;
  }
}

@media (max-width: 768px) {
  .zigzag-timeline {
    flex-direction: column;
  }
  
  .timeline-left {
    width: 100%;
    position: relative;
    height: auto;
    border-right: none;
    border-bottom: 1px solid rgba(122, 56, 200, 0.2);
  }
  
  .timeline-right {
    width: 100%;
    padding: 60px 30px;
    max-height: none;
    overflow-y: visible;
  }
  
  .timeline-track {
    left: 30px;
  }
  
  .timeline-items-container {
    padding-left: 20px;
  }
  
  .timeline-node {
    width: 50px;
    height: 50px;
  }
  
  .timeline-node svg {
    width: 24px;
    height: 24px;
  }
  
  .timeline-title {
    font-size: 36px;
  }
  
  .timeline-stats {
    gap: 30px;
  }
  
  .stat-value {
    font-size: 32px;
  }
}
