/* ===== PROJECTS SECTION - MODERN REDESIGN ===== */

.projects-section {
  position: relative;
  background: linear-gradient(135deg, #fafafa 0%, #ffffff 50%, #f8f9fa 100%);
  overflow: hidden !important;
}

/* Background Shapes */
.projects-bg-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 0;
  pointer-events: none;
}

.bg-circle {
  position: absolute;
  opacity: 0.05;
  animation: floatCircle 12s ease-in-out infinite;
}

.bg-circle-1 {
  width: 450px;
  height: 450px;
  background: radial-gradient(circle, #7a38c8, transparent);
  border-radius: 50%;
  top: -5%;
  right: 10%;
  filter: blur(90px);
}

.bg-circle-2 {
  width: 380px;
  height: 380px;
  background: radial-gradient(circle, #00bcd4, transparent);
  border-radius: 50%;
  bottom: -8%;
  left: 5%;
  filter: blur(80px);
  animation-delay: 4s;
}

.bg-circle-3 {
  width: 320px;
  height: 320px;
  background: radial-gradient(circle, #facc15, transparent);
  border-radius: 50%;
  top: 50%;
  left: 50%;
  filter: blur(70px);
  animation-delay: 8s;
}

@keyframes floatCircle {
  0%, 100% {
    transform: translate(0, 0) scale(1);
  }
  33% {
    transform: translate(25px, -25px) scale(1.08);
  }
  66% {
    transform: translate(-18px, 18px) scale(0.92);
  }
}

/* Main Wrapper */
.projects-wrapper {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  min-width: 100vw;
  height: 100vh;
  padding: 0 80px;
  gap: 60px;
  box-sizing: border-box;
}

/* ===== LEFT SECTION ===== */
.projects-left {
  flex: 0 0 400px;
  display: flex;
  flex-direction: column;
}

.projects-content-left {
  display: flex;
  flex-direction: column;
  gap: 28px;
}

/* Badge */
.project-badge {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 10px 20px;
  background: rgba(122, 56, 200, 0.08);
  border-radius: 50px;
  width: fit-content;
  border: 1px solid rgba(122, 56, 200, 0.15);
  transition: all 0.3s ease;
}

.project-badge:hover {
  background: rgba(122, 56, 200, 0.12);
  transform: translateY(-2px);
}

.badge-icon {
  font-size: 18px;
}

.badge-label {
  font-size: 13px;
  font-weight: 600;
  color: #7a38c8;
  text-transform: uppercase;
  letter-spacing: 1.5px;
}

/* Title */
.projects-main-title {
  font-size: 4rem;
  font-weight: 800;
  line-height: 1.1;
  color: #1a1a1a;
  margin: 0;
  letter-spacing: -1px;
}

.title-gradient {
  background: linear-gradient(135deg, #7a38c8 0%, #00bcd4 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  display: inline-block;
  animation: gradientSlide 3s ease infinite;
  background-size: 200% 200%;
}

@keyframes gradientSlide {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

/* Description */
.projects-description {
  margin: 0;
}

.projects-description p {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.8;
  color: #5a5a5a;
  margin: 0;
}

/* GitHub Button */
.github-link {
  text-decoration: none;
  width: fit-content;
}

.github-button {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 14px 28px;
  background: #1a1a1a;
  color: white;
  border: 2px solid #1a1a1a;
  border-radius: 12px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.github-button:hover {
  background: transparent;
  color: #1a1a1a;
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.github-icon {
  font-size: 20px;
}

.external-icon {
  font-size: 14px;
  transition: transform 0.3s ease;
}

.github-button:hover .external-icon {
  transform: translate(3px, -3px);
}

/* Stats */
.projects-stats {
  display: flex;
  align-items: center;
  gap: 25px;
  margin-top: 10px;
}

.stat-item {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.stat-number {
  font-size: 36px;
  font-weight: 800;
  background: linear-gradient(135deg, #7a38c8 0%, #00bcd4 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1;
}

.stat-label {
  font-size: 12px;
  font-weight: 600;
  color: #6a6a6a;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: black;
}

.stat-divider {
  width: 2px;
  height: 40px;
  background: linear-gradient(to bottom, transparent, #7a38c8, transparent);
}

/* ===== RIGHT SECTION ===== */
.projects-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-y: auto;
  max-height: 90vh;
  padding-right: 10px;
}

.projects-right::-webkit-scrollbar {
  width: 6px;
}

.projects-right::-webkit-scrollbar-track {
  background: rgba(122, 56, 200, 0.05);
  border-radius: 10px;
}

.projects-right::-webkit-scrollbar-thumb {
  background: rgba(122, 56, 200, 0.3);
  border-radius: 10px;
}

.projects-right::-webkit-scrollbar-thumb:hover {
  background: rgba(122, 56, 200, 0.5);
}

.projects-grid-container {
  width: 100%;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 25px;
  padding: 10px;
}

/* Project Cards */
.project-card {
  position: relative;
  background: white;
  border-radius: 16px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  border: 2px solid rgba(122, 56, 200, 0.08);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
}

.project-card:hover {
  transform: scale(1.03);
  box-shadow: 0 12px 35px rgba(122, 56, 200, 0.15);
  border-color: rgba(122, 56, 200, 0.2);
}

/* Project Image */
.project-image-wrapper {
  position: relative;
  width: 100%;
  height: 220px;
  overflow: hidden;
  background: linear-gradient(135deg, rgba(122, 56, 200, 0.05), rgba(0, 188, 212, 0.05));
}

.project-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}

.project-card:hover .project-image {
  transform: scale(1.1);
}

/* Project Overlay */
.project-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to bottom, transparent 0%, rgba(0, 0, 0, 0.7) 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 15px;
}

.project-card:hover .project-overlay {
  opacity: 1;
}

.project-category {
  background: rgba(122, 56, 200, 0.9);
  color: white;
  padding: 6px 14px;
  border-radius: 20px;
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  backdrop-filter: blur(10px);
}

.project-link-btn {
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #1a1a1a;
  font-size: 18px;
  transition: all 0.3s ease;
  text-decoration: none;
  backdrop-filter: blur(10px);
}

.project-link-btn:hover {
  background: #7a38c8;
  color: white;
  transform: scale(1.1) rotate(5deg);
}

/* Project Content */
.project-content {
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.project-title {
  font-size: 18px;
  font-weight: 700;
  color: #1a1a1a;
  margin: 0;
  line-height: 1.3;
}

.project-description {
  font-size: 13px;
  color: #6a6a6a;
  margin: 0;
  line-height: 1.6;
}

.project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 5px;
}

.tech-tag {
  padding: 5px 12px;
  background: rgba(122, 56, 200, 0.08);
  color: #7a38c8;
  border-radius: 6px;
  font-size: 11px;
  font-weight: 600;
  border: 1px solid rgba(122, 56, 200, 0.15);
  transition: all 0.3s ease;
}

.project-card:hover .tech-tag {
  background: rgba(122, 56, 200, 0.12);
  border-color: rgba(122, 56, 200, 0.25);
}

/* Glow Effect */
.project-glow {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #7a38c8, #00bcd4, #facc15);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.project-card:hover .project-glow {
  opacity: 0.7;
  box-shadow: 0 0 20px rgba(122, 56, 200, 0.5);
}

/* ===== RESPONSIVE DESIGN ===== */

@media (max-width: 1400px) {
  .projects-wrapper {
    gap: 50px;
    padding: 0 60px;
  }

  .projects-main-title {
    font-size: 3.5rem;
  }

  .projects-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
}

@media (max-width: 1200px) {
  .projects-wrapper {
    flex-direction: column;
    padding: 60px 40px;
    height: auto;
    min-height: 100vh;
  }

  .projects-left {
    flex: none;
    width: 100%;
  }

  .projects-right {
    flex: none;
    width: 100%;
    max-height: none;
    overflow-y: visible;
  }

  .projects-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .projects-main-title {
    font-size: 3rem;
  }
}

@media (max-width: 768px) {
  .projects-wrapper {
    padding: 40px 30px;
  }

  .projects-main-title {
    font-size: 2.5rem;
  }

  .projects-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .project-image-wrapper {
    height: 200px;
  }

  .github-button {
    padding: 12px 24px;
    font-size: 14px;
  }

  .stat-number {
    font-size: 28px;
  }
}

@media (max-width: 480px) {
  .projects-wrapper {
    padding: 30px 20px;
  }

  .projects-main-title {
    font-size: 2rem;
  }

  .project-badge {
    padding: 8px 16px;
  }

  .badge-label {
    font-size: 11px;
  }

  .projects-description p {
    font-size: 16px;
  }

  .project-content {
    padding: 16px;
  }

  .project-title {
    font-size: 16px;
  }

  .project-description {
    font-size: 12px;
  }
}
