/* Scroll Container */
.scroll-container {
    display: flex;
    overflow-x: auto; /* Allow horizontal scrolling */
    overflow-y: hidden; /* Hide vertical scroll */
    height: 100vh; /* Full height */
    white-space: nowrap;
    scroll-snap-type: x mandatory; /* Snap sections for better experience */
    scroll-behavior: smooth; /* Smooth horizontal scrolling */
  }
  
  /* Hide default scrollbar and style custom one */
  .scroll-container::-webkit-scrollbar {
    height: 10px; /* Horizontal scrollbar height */
  }
  
  .scroll-container::-webkit-scrollbar-thumb {
    background-color: #888; /* Scrollbar thumb color */
    border-radius: 10px; /* Rounded edges */
  }
  
  .scroll-container::-webkit-scrollbar-thumb:hover {
    background-color: #555; /* Darker color on hover */
  }
  
  .scroll-container::-webkit-scrollbar-track {
    background-color: #f0f0f0; /* Scrollbar track background */
  }
  
  .section {
    min-width: 100vw; 
    height: 100vh; 
    scroll-snap-align: start; 
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f0f0f0;
  }
  
  .section:nth-child(odd) {
    background-color: #ececec;
  }
  
  .scroll-container {
    display: flex;
    overflow-x: scroll;
    scroll-behavior: smooth;
  }
  
/* Bottom Navigation */
.bottom-nav {
  position: fixed;
  bottom: 25px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, 
    rgba(122, 56, 200, 0.95) 0%, 
    rgba(0, 188, 212, 0.95) 100%
  );
  border-radius: 25px;
  backdrop-filter: blur(20px);
  box-shadow: 
    0 8px 32px rgba(122, 56, 200, 0.3),
    0 0 0 1px rgba(255, 255, 255, 0.1) inset;
  z-index: -1;
}

.nav-list {
  list-style-type: none;
  display: flex;
  gap: 4px;
  margin: 0;
  padding: 8px 12px;
  position: relative;
  z-index: 1;
}

.nav-item {
  position: relative;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0;
  padding: 8px 12px;
  border-radius: 16px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  overflow: hidden;
}

.nav-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.nav-item:hover::before {
  opacity: 1;
}

.nav-item:hover {
  transform: translateY(-3px);
}

.nav-icon-wrapper {
  position: relative;
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  transition: all 0.3s ease;
}

.nav-item:hover .nav-icon-wrapper {
  background: rgba(255, 255, 255, 0.3);
  transform: scale(1.15);
}

.nav-icon {
  font-size: 18px !important;
  color: white;
  position: relative;
  z-index: 2;
  transition: all 0.3s ease;
}

.icon-ripple {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  opacity: 0;
  transition: all 0.5s ease;
}

.nav-item:hover .icon-ripple {
  width: 50px;
  height: 50px;
  opacity: 0.5;
}

.nav-label {
  display: none;
}

.nav-indicator {
  position: absolute;
  bottom: 2px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #facc15, #fff);
  border-radius: 2px;
  transition: width 0.3s ease;
  box-shadow: 0 0 8px rgba(250, 204, 21, 0.6);
}

.nav-item:hover .nav-indicator {
  width: 70%;
}

/* Active State */
.nav-item.active {
  background: rgba(255, 255, 255, 0.25);
}

.nav-item.active .nav-icon-wrapper {
  background: rgba(250, 204, 21, 0.3);
  box-shadow: 0 0 20px rgba(250, 204, 21, 0.6);
  animation: pulse-active 2s ease-in-out infinite;
}

@keyframes pulse-active {
  0%, 100% {
    box-shadow: 0 0 20px rgba(250, 204, 21, 0.6);
  }
  50% {
    box-shadow: 0 0 30px rgba(250, 204, 21, 1);
  }
}

.nav-item.active .nav-icon {
  color: #facc15;
  filter: drop-shadow(0 0 4px rgba(250, 204, 21, 0.8));
  animation: bounce-icon 0.6s ease;
}

@keyframes bounce-icon {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
}

.nav-item.active .nav-indicator {
  width: 80%;
  background: linear-gradient(90deg, #facc15, #fff);
  box-shadow: 0 0 12px rgba(250, 204, 21, 0.8);
}

/* Navigation Glow Effect */
.nav-glow {
  position: absolute;
  top: -15px;
  left: 50%;
  transform: translateX(-50%);
  width: 120px;
  height: 120px;
  background: radial-gradient(circle, rgba(250, 204, 21, 0.3) 0%, transparent 70%);
  border-radius: 50%;
  pointer-events: none;
  animation: glow-pulse 3s ease-in-out infinite;
  z-index: -2;
}

@keyframes glow-pulse {
  0%, 100% {
    opacity: 0.5;
    transform: translateX(-50%) scale(1);
  }
  50% {
    opacity: 0.8;
    transform: translateX(-50%) scale(1.1);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .bottom-nav {
    bottom: 15px;
  }

  .nav-list {
    gap: 3px;
    padding: 6px 10px;
  }

  .nav-item {
    padding: 6px 10px;
  }

  .nav-icon-wrapper {
    width: 26px;
    height: 26px;
  }

  .nav-icon {
    font-size: 16px !important;
  }
}

